import {SAVE_CLIENT} from '../constants'
import {getCurrentDate} from '../lib/functions'
const initialState = {
    client:{ 
        idResident: true, //    
        lname: '',  // validate required, size & characters 
        sname: '',  // validate required, size & characters
        fname: '',  // validate required, size & characters
        ipn: '1234567890',    // validate required, size & characters
        dob: getCurrentDate(),
        doc:{
          type:'1',   //    id-    
                     // seria     - 
          seria: 'seria',
          no:'221',
          dtget: getCurrentDate(),
          source: ' ',                  
        },
        addr: ' 12',
        phone: '551234567',
        email: 'ivan@mail.com',
    },
    msgLNameValid: '',
    msgFNameValid: '',
    msgSNameValid: '',
    msgIpnValid: '',
    msgDOBValid: '',
    msgDocTypeValid: '',
    msgDocSeriaValid: '',
    msgDocNoValid: '',
    msgDocDtGetValid: '',
    msgDocSourceValid: '',
    msgAddrValid: '',
    msgPhoneValid: '',
    msgEmailValid:'',
    formValid:false,
}


export const reducerClient = (state = initialState, action) => {
    switch(action.type){
        case SAVE_CLIENT:
            return action.payload
         default:
            return state        
    }
}